.rm-canvas-layer {
  user-select: none;
  position: absolute !important;
  border: none;
  padding: 0;
  margin: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  direction: ltr;
  display: block;
  opacity: 1;
}
.rm-popup {
  margin-left: 12px;
  margin-top: -6px;
  pointer-events: none;
  position: absolute;
  z-index: 1;
  display: flex;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.8);
  transform-origin: 50% 100%;
  animation: popup-intro-animation-down 200ms ease-in-out;
  box-sizing: inherit;
}
.rm-popup .popup-main-container {
  box-sizing: border-box;
  color: #323232;
  font-size: 14px;
  font-family: 'Avenir Next W00', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.3em;
  outline: none;
  pointer-events: auto;
  position: relative;
  z-index: 1;
  background-color: #fff;
  display: flex;
  flex-flow: column nowrap;
}
.rm-popup .popup-main-container header {
  position: relative;
  font-size: 12px;
  align-items: flex-start;
  justify-content: space-between;
  display: flex;
  flex: 0 0 auto;
}
.rm-popup .popup-main-container header h2 {
  font-size: 14px;
  padding: 12px 15px;
  margin: 0 auto 0 0;
  display: block;
  flex: 1;
  word-break: break-word;
  word-wrap: break-word;
  cursor: pointer;
}
.rm-popup .popup-main-container header .popup-header-buttons {
  padding: 0 7px;
  margin: 0 0 0 auto;
  display: flex;
}
.rm-popup .popup-main-container article {
  display: flex;
  flex-flow: column nowrap;
  flex: 1 1 auto;
  font-size: 12px;
  font-weight: 400;
  margin: 0 15px 12px;
  overflow: auto;
  overflow-x: hidden;
}
.rm-popup .popup-main-container article .attribute-str-constainer ul {
  list-style: none;
  padding: 0;
}
.rm-popup .popup-main-container .feature-buttons {
  padding: 0 7px;
  align-items: center;
  display: flex;
  justify-content: flex-end;
}
.rm-popup .popup-main-container .feature-buttons section.navigation {
  padding: 0 7px;
  align-items: center;
  display: flex;
  justify-content: flex-end;
}
.rm-popup .popup-main-container .feature-buttons section.navigation .navigation-buttons {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.rm-popup .popup-main-container .feature-buttons section.navigation .navigation-buttons .pagination-page-text {
  white-space: nowrap;
}
.rm-popup .popup-main-container .feature-buttons section.navigation .navigation-buttons .navigation-buttons {
  font-weight: 400;
  font-size: 12px;
  position: relative;
  display: flex;
  justify-content: flex-start;
  flex: 0 0 auto;
}
.rm-popup .popup-pointer {
  position: absolute;
  width: 0;
  height: 0;
  top: 0;
  left: 0;
  margin: 0 0 0 -6px;
}
.rm-popup .popup-pointer .popup-pointer-direction {
  background-color: #fff;
  content: '';
  position: absolute;
  width: 12px;
  height: 12px;
  transform: scale(1.5, 0.75) rotate(45deg);
  top: 1px;
  left: 0;
}
.rm-popup .shadow {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.8);
}
.rm-popup .button {
  padding: 12px 7px;
  line-height: 1.3em;
  cursor: pointer;
  user-select: none;
  color: #6e6e6e;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  background-image: none;
}
.rm-popup.is-docked {
  margin: 15px 15px 30px 15px;
  animation: esri-docking-animation 250ms ease-out;
}
