@import '../../style/themes/default';

@canvas-layer-prefix-cls: ~'@{prefix}-canvas-layer';

.@{canvas-layer-prefix-cls} {
  user-select: none;
  position: absolute !important;
  border: none;
  padding: 0;
  margin: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  direction: ltr;
  display: block;
  opacity: 1;
}
