.mapSpin {
  width: 100%;
  height: 100%;

  >div {
    width: 100%;
    height: 100%;
  }
}

.header {
  padding: 10px;
  float: right;
  text-align: right;
  position: relative;
  z-index: 2;
}

.statusRadio {
  width: 90px;
  padding: 3px 0;
  text-align: left;

}

.mapWrapper {
  width: 100%;
