.container_direction.top-left {
  transform: translate(0%, 0%);
}
.container_direction.top-middle {
  transform: translate(-50%, 0);
}
.container_direction.top-right {
  transform: translate(-100%, 0);
}
.container_direction.bottom-left {
  transform: translate(0%, -100%);
}
.container_direction.bottom-middle {
  transform: translate(-50%, -100%);
}
.container_direction.bottom-right {
  transform: translate(-100%, -100%);
}
.container_direction.middle {
  transform: translate(-50%, -50%);
}
