@import '../../style/themes/default';

@tip-prefix-cls: ~'@{prefix}-tip';

.container_direction {
  &.top-left {
    transform: translate(0%, 0%);
  }

  &.top-middle {
    transform: translate(-50%, 0);
  }

  &.top-right {
    transform: translate(-100%, 0);
  }

  &.bottom-left {
    transform: translate(0%, -100%);
  }

  &.bottom-middle {
    transform: translate(-50%, -100%);
  }

  &.bottom-right {
    transform: translate(-100%, -100%);
  }

  &.middle {
    transform: translate(-50%, -50%);
  }
}

.@{tip-prefix-cls} {
  &-content {
    position: absolute;
    .container_direction;
  }

  &-point {
    position: absolute;
    .container_direction;

    .base {
      border-radius: 50px;
      background-color: green;
      height: 20px;
      width: 20px;
    }
  }
}
