.mapSpin {
  width: 100%;
  height: 100%;
}
.mapSpin > div {
  width: 100%;
  height: 100%;
}
.header {
  padding: 10px;
  float: right;
  text-align: right;
  position: relative;
  z-index: 2;
}
.statusRadio {
  width: 90px;
  padding: 3px 0;
  text-align: left;
}
.mapWrapper {
  width: 100%;
  height: 100%;
}
/******************************第一屏弹出窗口*********************************************/
.po {
  border-style: solid;
  border-color: transparent;
  border-image: url("../images/科技tip框.png");
  padding: 0;
}
.po > div {
  height: 100%;
  background-color: #000f4c;
  opacity: 0.7;
  margin-top: -265px;
  margin-left: -109px;
  margin-right: -238px;
  margin-bottom: -160px;
  font-family: MicrosoftYaHei;
  font-size: 13px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 2px;
  line-height: 36px;
  color: #ffffff;
  padding: 0 20px;
  border: 0.1px solid #ffffff;
  border-radius: 3% 5% 1% 30%;
  white-space: nowrap;
}
.po_name {
  width: 80px;
  height: 20px;
  text-align: center;
  line-height: 100%;
  border-bottom: 1px solid #0090ff;
  margin-top: -8px;
}
.po_plan {
  max-height: 300px;
  margin: 5px 0px;
  overflow-y: auto;
}
.po_plan_basic {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.po_plan_basic_keys {
  flex: 0.3;
}
.po_plan_basic_vals {
  flex: 1;
}
.po_plan_stat {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 5px 0px;
}
.po_plan_stat_keys {
  flex: 0.3;
  line-height: 35px;
}
.po_plan_stat_vals {
  flex: 1;
}
.po_plan_stat_vals_zt {
  display: flex;
  flex-direction: column;
}
.po_plan_realTime {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.po_plan_realTime_data {
  width: 49%;
  display: flex;
  flex-direction: column;
}
.po_plan_realTime_data_xs {
  display: flex;
  justify-content: space-between;
  width: 65%;
}
.po_plan_realTime_data_xs_keys {
  flex: 0.3;
}
.po_plan_realTime_data_xs_vals {
  width: 100px;
  display: flex;
  font-family: "webfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #00ffff;
}
.po_plan_realTime_data_xs_vals > span {
  font-family: MicrosoftYaHei;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 1px;
  color: #00ffff;
  margin-left: 16px;
}
.po_plan_realTime_imgs {
  margin-top: 5px;
  margin-left: 5px;
  width: 180px;
  height: 100px;
  background-color: #001b5c;
}
.po_plan_realTime_imgs > img {
  width: 180px;
  height: 100px;
}
.po_plan::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 10px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}
.po_plan::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 2px;
  -webkit-box-shadow: inset 0 0 5px #000000;
  background: #0090ff;
}
.po_plan::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  background: rgba(2, 105, 199, 0.1);
}
.cal {
  background: url(../images/关闭.png);
  width: 11px;
  height: 11px;
  position: absolute;
  right: 75px;
  top: 10px;
  cursor: pointer;
}
.bjzt {
  display: flex;
  flex-direction: column;
}
.bjzt > img {
  width: 11px !important;
  height: 14px !important;
}
.bjzt > i {
  width: 16px;
  height: 11px;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 40px;
  letter-spacing: 1px;
  color: #0090ff;
}
/******************************第三屏视频窗口*********************************************/
.vi {
  width: 160px;
  height: 215px;
  background-color: rgba(14, 70, 152, 0.5);
  border: solid 1px #00ffff;
  border-radius: 5px;
}
.vi_gb {
  width: 90%;
  text-align: end;
  font-size: 13px;
  color: #00ffff;
  cursor: pointer;
}
.vi_plan {
  width: 90%;
  padding: 7px 10px;
  max-height: 170px;
  overflow-y: auto;
}
.vi_plan > div {
  font-family: MicrosoftYaHei;
  font-size: 14px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
}
.vi_plan_x {
  cursor: pointer;
  color: #fff;
  margin: 7px;
}
.vi_plan_x:hover {
  color: #00ffff;
}
.vi_plan::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 3px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}
.vi_plan::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 1px;
  -webkit-box-shadow: inset 0 0 5px #000000;
  background: #00ffff;
}
.vi_plan::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 1px;
  background: rgba(2, 105, 199, 0.1);
}
