@import '../../style/themes/default';

@map-view-prefix-cls: ~'@{prefix}-map-view';

.@{map-view-prefix-cls} {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;

  &-logo {
    position: absolute;
    right: 20px;
    bottom: 20px;
  }

  .esri-view-surface {
    outline: 0px;
  }
}

.esri-component.esri-attribution.esri-widget {
  display: none;
}

.esri-view .esri-view-surface--inset-outline:focus::after {
  content: '';
  box-sizing: border-box;
  position: absolute;
  z-index: 999;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  /* outline:auto 2px Highlight; */
  /* outline:auto 5px -webkit-focus-ring-color; */
  outline-offset: -3px;
  pointer-events: none;
  overflow: hidden;
}

